import { combineReducers } from 'redux';
import { productReducer, selectedProductReducer,filteredProductsReducer, searchWordReducer, searchProductReducer, cartProductReducer, orderProductReducer, orderCartProductReducer, orderPendingProductReducer } from './productReducer';

const reducers = combineReducers({
    allProducts:productReducer,
    allCart:cartProductReducer,
    orderProducts:orderProductReducer,
    orderAllProducts:orderCartProductReducer,
    orderAllPendingProducts:orderPendingProductReducer,
    product:selectedProductReducer,
    searchWord:searchWordReducer,
    searchProducts:searchProductReducer,
  
});

export default reducers;