
import React,{useEffect,useState} from 'react';
import ProductModal from '../ProductModal/ProductModal';
import './AdminOrder.css';
import { useDispatch, useSelector } from 'react-redux';
import { setProducts,searchWord,setCart,setOrder } from '../../redux/actions/productActions';
import http from '../../http';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import DataTable from 'react-data-table-component';
var load = 0;
function AdminOrder() {
    const [isLoading, setIsLoading] = useState(0);
    const allOrder = useSelector((state) => state.orderAllPendingProducts.products);
   
    const columns = [
        {
            name: "Order Id",
            selector: row => row.orderid,
            sortable:true
        },
        {
            name: "Customer Name",
            selector: row => row.customer,
            sortable:true
        },
        {
            name: "Shipping Address",
            selector: row => row.address,
            sortable:true
        },
        {
            name: "Order Date",
            selector: row => row.orderdate,
            sortable:true
        },
        {
            name: "Actions",
            button: true,
            cell: (row) => (
                <>
                <button
                    className="btn btn-primary me-1"
                    onClick={(e) => handleButtonClick(e, row.orderid)}
            >
                <i className="fa-solid fa-check"></i>
                </button>
                <button
                className="btn btn-warning"
            >
                <i className="fa-solid fa-print"></i>
              
                </button>
                </>
            )
           
        }
    ]
    const data = [];
    const handleButtonClick = (e, id) => {
        e.preventDefault();
        console.log("Row Id", id);
    };
    const setData = () => {
        setIsLoading(1);
            allOrder.map((order, index) => { 
                console.log(order.id);
                const newOrder = {
                    orderid: order.id,
                    customer: `${order.first_name} ${order.middle_initial} ${order.last_name}`,
                    address:`${order.address_line_1} ${order.barangay} ${order.city} ${order.province}`,
                    orderdate:`${order.date_order}`
                }
    
                data.push(newOrder);
              
               
               
            })
        
            setIsLoading(0);
    }
    
  
    useEffect(() => {
        setData(); 
    },[allOrder]);
    return (
        <>
            <div className="row mt-3">
                <div className="col-12">
                    <div className="row">
                    <div className="col-12 d-flex justify-content-between">
                            <h1 className="h2">List of Orders</h1> 
                           
                        </div>
                    <div className="col-12">
                    <div className="table-responsive">
                    <div className="container mt-4">
                    <div className="row header header-table" >

                    <h4>List of Pending Order </h4>
                    </div>
                            <DataTable
                                columns={columns}
                                data={data}
                                
                            >
                            </DataTable>
                        </div> 
                    </div>
                    </div>
                    </div>
                </div>
            </div>
           
           
        </>
    );
}

export default AdminOrder;