
import React from 'react';

function ProductContent() {
    return (
        <>
         <section>
        <div className="container-fluid">
          <div className="row">
            <div className="col-xxl-4 col-xl-4 col-lg-4">
          
              <div className="row">
             
                <div className="mt-2 col-xxl-12 col-xl-12 col-lg-12">
                  <div className="accordion" id="accordionProduct">
                    <div className="accordion-item">
                      <h2 className="accordion-header" id="headingOne">
                        <button
                          className="accordion-button accordion-color"
                          type="button"
                          data-bs-toggle="collapse"
                          data-bs-target="#collapseProduct"
                          aria-expanded="true"
                          aria-controls="collapseProduct"
                        >
                          Category
                        </button>
                      </h2>
                      <div
                        id="collapseProduct"
                        className="accordion-collapse collapse show"
                        aria-labelledby="headingOne"
                        data-bs-parent="#accordionProduct"
                      >
                        <div className="accordion-body">
                       
                          <ul className="list-group" id="category-list">
                          
                          </ul>

                       
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
            
                <div
                  className="mt-lg-2 mt-md-2 mt-sm-2 mt-2 col-xxl-12 col-xl-12 col-lg-12"
                >
                  <div className="accordion" id="accordionPrice">
                    <div className="accordion-item">
                      <h2 className="accordion-header" id="headingOne">
                        <button
                          className="accordion-button accordion-color"
                          type="button"
                          data-bs-toggle="collapse"
                          data-bs-target="#collapsePrice"
                          aria-expanded="true"
                          aria-controls="collapsePrice"
                        >
                          Price
                        </button>
                      </h2>
                      <div
                        id="collapsePrice"
                        className="accordion-collapse collapse show"
                        aria-labelledby="headingOne"
                        data-bs-parent="#accordionPrice"
                      >
                        <div className="accordion-body">
                          <div className="price-range">
                            <div className="row">
                              <div className="col-lg-12">
                                <label for="customRange2" className="form-label"
                                  >From: <span id="from-value">0</span></label
                                >
                                <input
                                  id="from-input"
                                  type="range"
                                  className="form-range"
                                  value="0"
                                  min="0"
                                  max="100"
                                />
                              </div>
                              <div className="col-lg-12">
                                <label for="customRange2" className="form-label"
                                  >To: <span id="to-value">100</span></label
                                >
                                <input
                                  id="to-input"
                                  type="range"
                                  className="form-range"
                                  value="100"
                                  min="0"
                                  max="100"
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
             
              </div>
            
            </div>
            <div className="col-xxl-8 col-xl-8 col-lg-8">
            
              <div className="row" id="dress-list"></div>
          
            </div>
          </div>
        </div>
      </section>
        </>
    );
}

export default ProductContent;